import React, { useState } from 'react';
import "./index.css";
import OutlinedInput from '@mui/material/OutlinedInput';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Axios from '../../../api/Axios';
import { useCallback } from 'react';
import { useEffect } from 'react';
import { useParams } from 'react-router-dom';
import Loader from '../../../components/Loader';
import triggerSwalAlert from '../../../helpers/triggerSwalAlert';
import { useNavigate } from 'react-router-dom';
import moment from 'moment';

const axios = new Axios();

const OrderView = () => {
    const [action, setAction] = useState("");
    const navigate = useNavigate();
    const { id } = useParams();
    const [validId, setValidId] = useState(false)
    const [pending, setPending] = useState(true);
    const [order, setOrder] = useState({});
    const ITEM_HEIGHT = 48;
    const ITEM_PADDING_TOP = 8;
    const MenuProps = {
        PaperProps: {
            style: {
                maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
                width: 250,
            },
        },
    };

    const getOrderById = useCallback(async () => {
        setPending(true)
        let options = {
            method: "GET"
        }
        let result = await axios.callApi(`/order/getOrderById/${id}`, options)
        if (result.status == 200) {
            setPending(false)
            setOrder(result.data.data)
            setAction(result.data.data.orderStatus)
            setValidId(true)
        }
        else {
            setPending(false)
            triggerSwalAlert("Error", "Error while loading the order", "error")
            navigate(-1)
        }
    }, [])

    useEffect(() => {
        getOrderById()
    }, [])

    const actions = [
        {
            id: 2,
            value: 'COMPLETED'
        },
        {
            id: 3,
            value: 'PROCESSING'
        },
        {
            id: 4,
            value: 'ON HOLD'
        },
        {
            id: 5,
            value: 'CANCELLED'
        },
        {
            id: 6,
            value: 'REFUNDED'
        },
        {
            id: 7,
            value: 'FAILED'
        }
    ];

    const handleChange = async (event) => {
        if(!validId) return
        const { target: { value } } = event;
        setPending(true)
        setAction(value);
        let options = {
            method: "POST",
            body: {
                status: value,
                ids: [id]
            }
        }
        let result = await axios.callApi("/order/bulkUpdateOrderStatus", options);
        if(result.status == 200){
            setPending(false)
            triggerSwalAlert("Success", "Status Updated Successfully", "success")
        }
        else{
            setPending(false)
            triggerSwalAlert("Error", "Error while updating the status of order", "error")
        }
    };
    return (
        <>
            {pending ? <Loader /> : null}
            <div className='container orderViewContainer my-4'>
                <div className="card p-4">
                    <div className="orderTitle">Order {order?.orderToken} details</div>
                    <div className="paymentInfo">Payment via {order?.paymentMethod} </div>
                    <div className="row mt-4">
                        <div className="col-4">
                            <div className="rowTitle">General</div>
                            <form action="">
                                <div className="genTitle">Date created:</div>
                                <div>{moment(order?.placedAt).format("Do MMM yyyy h:mm a")}</div>
                                {/* <input type="date" min="1997-01-01" max="2050-12-31" value="2017-06-01" /> @
                        <input className='ms-1' type="time" value="10:00" max="12:00" /> */}
                                <div className="genTitle">Status:</div>
                                {/* <input type="text" /> */}
                                <FormControl sx={{ m: 1, width: 300 }}>
                                    <Select
                                        displayEmpty
                                        value={action}
                                        onChange={handleChange}
                                        defaultValue={action}
                                        input={<OutlinedInput />}
                                        renderValue={(selected) => {
                                            if (!selected) {
                                                return <em>Bulk actions</em>;
                                            }
                                            return selected;
                                        }}
                                        MenuProps={MenuProps}
                                        inputProps={{ 'aria-label': 'Without label' }}
                                    >
                                        {actions.map((item) => (
                                            <MenuItem
                                                key={item.id}
                                                value={item.value}
                                            >
                                                {item.value}
                                            </MenuItem>
                                        ))}
                                    </Select>
                                </FormControl>
                            </form>
                        </div>
                        <div className="col-4">
                            <div className="rowTitle">Billing</div>
                            <div className='billingDetails'>
                                <div className="customerName">{order?.addressDetails?.billingAddress?.firstName} {order?.addressDetails?.billingAddress?.lastName}</div>
                                <div className="customerAddress">{order?.addressDetails?.billingAddress?.streetAddress} {order?.addressDetails?.billingAddress?.landmark}</div>
                                <span className="customerCity">{order?.addressDetails?.billingAddress?.city}</span>
                                <span className="customerPincode">{order?.addressDetails?.billingAddress?.pincode}</span>
                                <div className="customerState">{order?.addressDetails?.billingAddress?.state}</div>
                                <div className="emailTitle">Email address:</div>
                                <div className="emailAddress"><a href={`mailto:${order?.addressDetails?.billingAddress?.email}`}>{order?.addressDetails?.billingAddress?.email}</a></div>
                                <div className="phoneTitle">Phone:</div>
                                <div className="phoneDetails">{order?.addressDetails?.billingAddress?.contact}</div>
                            </div>
                        </div>
                        <div className="col-4">
                            <div className="rowTitle">Shipping</div>
                            <div className='shippingDetails'>
                                <div className="customerName">{order?.addressDetails?.shippingAddress?.firstName} {order?.addressDetails?.shippingAddress?.lastName}</div>
                                <div className="customerAddress">{order?.addressDetails?.shippingAddress?.streetAddress} {order?.addressDetails?.shippingAddress?.landmark}</div>
                                <span className="customerCity">{order?.addressDetails?.shippingAddress?.city}</span>
                                <span className="customerPincode">{order?.addressDetails?.shippingAddress?.pincode}</span>
                                <div className="customerState">{order?.addressDetails?.shippingAddress?.state}</div>
                                <div className="emailTitle">Email address:</div>
                                <div className="emailAddress"><a href={`mailto:${order?.addressDetails?.shippingAddress?.email}`}>{order?.addressDetails?.shippingAddress?.email}</a></div>
                                <div className="phoneTitle">Phone:</div>
                                <div className="phoneDetails">{order?.addressDetails?.shippingAddress?.contact}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="card p-4 mt-4">
                    <div className="row">
                        <div className="col-8">Item</div>
                        <div className="col-1">Cost</div>
                        <div className="col-1">Qty</div>
                        <div className="col-1">Total</div>
                        <div className="col-1">18% GST</div>
                    </div>
                    <div className="card my-4 p-2">
                        {order?.orderDetails?.map((order) => {
                            return (
                                <div className="row">
                                    <div className="col-8">{order?.productName} {Object.values(order?.attributeCombination)}</div>
                                    <div className="col-1">&#8377; {(order?.productDiscountPrice - 18/100*order?.productDiscountPrice)?.toFixed(2)}</div>
                                    <div className="col-1">X {order?.count}</div>
                                    <div className="col-1">&#8377; {(order?.productDiscountPrice*order?.count - 18/100*order?.productDiscountPrice*order?.count)?.toFixed(2)}</div>
                                    <div className="col-1">&#8377; {(18/100*order?.productDiscountPrice*order?.count)?.toFixed(2)}</div>
                                </div>
                            )
                        })}
                    </div>
                    <div className="row">
                        <div className="col-10"></div>
                        <div className="col-1 mb-1">Items Subtotal:</div>
                        <div className="col-1 itemPrice">&#8377;{(order?.orderAmount - 18/100*order?.orderAmount)?.toFixed(2)}</div>
                        <div className="col-10"></div>
                        <div className="col-1 mb-1">18% GST:</div>
                        <div className="col-1 itemPrice">&#8377;{(18/100*order?.orderAmount)?.toFixed(2)}</div>
                        <div className="col-10"></div>
                        <div className="col-1 mb-1">Delivery Charges:</div>
                        <div className="col-1 itemPrice">&#8377;{(order?.deliveryCharges)?.toFixed(2)}</div>
                        <div className="col-10"></div>
                        <div className="col-1">Items Order Total:</div>
                        <div className="col-1 itemPrice">&#8377;{(order?.orderTotalAmount)?.toFixed(2)}</div>
                        <div className="col-7"></div>
                        <div className="col-5">
                            <hr style={{ border: "1px solid" }} />
                            <div className="row me-2">
                                <div className="col-8"></div>
                                <div className="col-2 fw-bold">Paid:</div>
                                <div className="col-2"><div className="fw-bold">&#8377;{(order?.orderTotalAmount)?.toFixed(2)}</div></div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </>
    )
}

export default OrderView;